@import url(https://fonts.googleapis.com/css2?family=Hind:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bangers&family=Passion+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab+Highlight:wght@700&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.intro_container {
  margin-bottom: 60px;
  padding: 10px 160px;
}

.intro_grid_column {
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  border-radius: 10px;
  background-color: rgba(253, 248, 242, 0.767);
}

.intro_grid_column div {
  padding: 20px;
}

.intro_grid_column p {
  font-size: 25px;
  text-align: justify;
  font-family: "Times New Roman", Times, serif;
}

.profile_pic {
  width: 100%;
  border-radius: 10px;
}

@media screen and (max-width: 800px) {
  .intro_container {
    margin-bottom: 30px;
    padding: 5px 15px;
  }
  .intro_grid_column div {
    padding: 10px;
  }

  .intro_grid_column p {
    font-size: 18px;
  }
}

.project_container {
  margin-bottom: 50px;
  padding: 10px 50px;
}

.project_container h2 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: center;
  font-size: 30px;
  color: white;
  padding-bottom: 50px;
}

.project_grid_column {
  display: grid;
  text-align: center;
  margin: auto;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 30px;
  gap: 30px;
}
.project {
  width: 100%;
  height: auto;
  padding: 20px 10px;
  border-radius: 10px;
  display: flex;
  align-items: flex-start;
  text-align: justify;
  background-color: antiquewhite;
}
.project h4 {
  font-size: 25px;
  text-align: center;
  align-items: flex-start;
  padding-bottom: 20px;
}
.project p {
  font-size: 18px;
  line-height: 22px;
  font-family: "hind";
}

.project1 {
}

.project2 {
}

.project3 {
  background-color: rgb(255, 145, 0);
}

.project3 {
  background-color: rgb(255, 145, 0);
}

.project4 {
  background-color: rgb(174, 0, 255);
}

@media screen and (max-width: 800px) {
  .project_container {
  }
  .project_grid_column {
    grid-template-columns: repeat(auto-fit, minmax(100px, 350px));
  }

  .project_container h2 {
    font-size: 20px;
  }
  .project {
    height: 250px;
  }
  .project h4 {
    font-size: 18px;
  }
  .project p {
    font-size: 15px;
  }
}

html {
  height: 100%;
}
body {
  min-height: 100%;
  margin: 0;
  font-family: "Roboto", sans-serif;
  background-image: linear-gradient(
    to right,
    rgb(95, 95, 95),
    rgb(21, 77, 122),
    rgb(95, 95, 95)
  );
}
* {
  margin: 0;
  padding: 0;
}
.welcome_container {
  height: 250px;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
  overflow: hidden;
}

.welcometext {
  margin: auto;
  height: auto;
  text-align: center;
}

.welcometext h1 {
  padding-top: 20px;
  padding-bottom: 30px;
  color: rgb(60, 223, 174);
  font-size: 140px;
  text-shadow: 5px 3px rgba(58, 65, 0, 0.863);
  font-family: "Passion one";
  -webkit-animation: 2s slide-up;
          animation: 2s slide-up;
}
.welcometext p {
  display: inline-block;
  font-size: 25px;
  color: white;
  padding-right: 5px;
}
.welcometext h5 {
  display: inline-block;
  color: rgba(255, 248, 248, 0.938);
  font-size: 40px;
  text-shadow: 1px 0px rgba(58, 65, 0, 0.863);
  font-family: "Zilla Slab Highlight";
  -webkit-animation: 3s slide-up;
          animation: 3s slide-up;
  cursor: pointer;
}
.welcometext h6 {
  padding-top: 10px;
  font-family: "Times New Roman", Times, serif;
  color: rgb(173, 173, 173);
  font-size: 20px;
}

@-webkit-keyframes slide-up {
  from {
    margin-top: 50%;
    height: 300%;
  }

  to {
    margin-top: 0%;
    height: 100%;
  }
}

@keyframes slide-up {
  from {
    margin-top: 50%;
    height: 300%;
  }

  to {
    margin-top: 0%;
    height: 100%;
  }
}

@media screen and (max-width: 800px) {
  .welcome_container {
    padding-bottom: 10px;
    height: 200px;
  }
  .welcometext h1 {
    font-size: 55px;
  }
  .welcometext p {
    font-size: 15px;
  }
  .welcometext h5 {
    font-size: 30px;
  }
  .welcometext h6 {
    font-size: 15px;
  }
}

body {
  display: table;
}
body > footer {
  display: table-row;
}
footer {
  bottom: 0;
  height: 120px;
  padding: 40px;
  background-image: linear-gradient(
    to right,
    rgb(145, 145, 145),
    rgb(28, 94, 148),
    rgb(145, 145, 145)
  );
  margin: auto;
}

.footer_grid {
  margin: auto;
  display: grid;
  text-align: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 400px));
}
.grid1,
.grid1 a {
  font-size: 15px;
  color: rgb(78, 78, 78);
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
.social a {
  text-decoration: none;
  color: black;
}
.social {
  padding: 0px 25px;
  font-size: 50px;
  display: inline-flex;
  justify-content: center;
  cursor: pointer;
}
.help_note {
  display: flex;
  font-size: 15px;
  padding-top: 20px;
  color: rgb(145, 145, 145);
  justify-content: center;
}

@media screen and (max-width: 800px) {
  footer {
    height: 140px;
    padding: 20px;
  }
  .grid1,
  .grid1 a {
    font-size: 10px;
    padding-bottom: 20px;
  }
  .help_note {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 10px;
  }
  .social {
    padding-top: 10px;
    font-size: 35px;
  }
  .grid3 {
    font-size: 12px;
  }
}

